.timer {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  border: 5px solid rgb(111, 100, 211);
  background-color: rgb(233, 118, 127);
  margin-top: 40px;
}

.field {
  display: flex;
  flex-direction: column;
  text-align: center;
  flex-shrink: 0;
  min-width: 80px;
  font-size: 12px;
}

.value {
  font-size: 52px;
  font-weight: 400;
}

.delimiter {
  position: relative;
  top: -12px;
  font-size: 60px;
  font-weight: 400;
}

.label {
  font-size: 16px;
  font-weight: 800;
}

.hidden {
  opacity: 0.2;
}
